.headerContainer {
  text-align: center;
}

.headerItem {
  display: inline-block;
  color: rgb(221, 221, 221);
  margin: 20px 25px;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
  font-size: 16px;
  &:hover {
    color: rgb(245, 245, 153);
  }
}

.selected {
  color: rgb(245, 245, 153);
}

@media only screen and (max-width: 480px) {
  .headerItem {
    display: inline-block;
    color: rgb(221, 221, 221);
    margin: 20px 19px;
    font-weight: bold;
    cursor: pointer;
    transition: 0.3s;
    font-size: 14px;
    &:hover {
      color: rgb(245, 245, 153);
    }
  }
}
